.button-see{
    /* display: block; */
    width: 20%;
    height: 50px;
    font-size: 14px;
    text-align: center;
    outline: none;
    border: 1px solid rgb(200, 235, 0);
    background-color: #fff;
    border-radius: 2px;
    color: rgb(200, 235, 0);
    cursor: pointer;
    transition: all .25s;
    z-index: 1;
    position: relative;
    
  }
  
  .button-see::before{
    content: '';
    position: absolute;
    width: 100%;
    height: 0;
    left: 0;
    bottom: 0;
    border: transparent;
    border-radius: 2px;
    background-image: linear-gradient(to right, 
    rgb(243, 239, 23), rgb(235, 172, 0));
    transition: all .25s;
    z-index: -1;
  }
  
  .button-see:hover{
    border:1px solid transparent;
    color: #fff;
  }
  
  .button-see:hover::before{
    height: 100%;
  } 
