.contenedore{
    height: calc(60vh);
    width: 100vw;
    display: flex;
    justify-content:center;
    align-items: center;
    
}

.contenedore figure {
    position: relative;
    height: 400px;
    width: 480px;
    overflow: hidden;
    
}
.contenedore figure img{
    width: 100%;
    height: 100%;
    transition: all 500ms ease-out; 
}
.contenedore figure .capar{
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    transition: all 500ms ease-out;
    opacity: 0;
    visibility: hidden;
    text-align:center;
}
.contenedore figure:hover > .capar{
    opacity: 1;
    visibility: visible;
}

.contenedore figure:hover > img{
    transform: scale(1.05);
}

.contenedore figure .capar h3{
    color: #ffa66b;
    font-weight: 400;
    margin-bottom: 120px;
    transition: all 500ms ease-out;
    margin-top:170px;
    font-family:'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif ; 
    font-size: 25px;
}

.contenedore figure .capar a{
    padding: 12px 24px;
    font-size: 16px;
    background: none;
    border-style: none;
    border: 1px solid #ffa66b;
    color: #ffa66b;
    transition: background-color 300ms ;
    cursor: pointer;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    border-radius: 3px;

}

.contenedore figure .capar a:hover{
    background-color:#ffa66b;
    color: #011c39;
    
}

.contenedore figure a{
    text-decoration: none;
}

